body {
  font-family: 'Lato', sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.push-right {
  padding-right: 10px;
}

@media (max-width: 767px) {
  .mobile-hide {
    display: none !important;
  }

  .mobile-full {
    width: 100% !important;
  }
}

@media (min-width: 768px) {
  .desktop-hide {
    display: none;
  }
}

.app-menu-icon {
  position: absolute;
  top: 25px;
  right: 25px;
  cursor: pointer;
}